.Friends {
  padding: 1rem;
  grid-area: n;
  background-color: #009e6b;
  border-radius: 2px;
}
.title {
  display: block;
  margin-bottom: .5em;
  font-size: 1.5em;
  color: #ffffff;
}

.list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  list-style: none;
  margin: 0;
  padding: 0;
}

.item {
  width: 60px;
  margin: 0 0 1em;

  a {
    display: block;
    color: #ffffff;
    text-align: center;
    line-height: 1;
    text-overflow: ellipsis;
    overflow: hidden;
  }
}

.ava {
  display: block;
  width: 60px;
  height: 60px;
  overflow: hidden;
  margin: 0 auto 0.2rem;
  padding: .5em;
  box-sizing: border-box;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.15);
}
