@import "./data";

html {
  height: 100%;
}

body {
  width: 100%;
  min-width: 320px;
  height: 100%;
  padding: 1rem;
  box-sizing: border-box;
  line-height: 1.4;
  font-family: 'Source Sans Pro', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: darkslategray;
}

h1,h2,h3,h4,h4 {
  margin: 0 0 .5em;
}

img {
  max-width: 100%;
  height: auto;
}

a {
  color: inherit;
  text-decoration: none;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
  monospace;
}

.app-wrapper {
  max-width: 1000px;
  margin: 0 auto;
  display: grid;
  grid-template-areas: "h h h" "s c c";
  grid-template-rows: 3.75rem 1fr;
  grid-template-columns: 3fr 9fr;
  grid-gap: 1rem;

  @media all and(max-width: 480px) {
    grid-template-areas: "h h h" "s s s" "c c c";
    grid-template-columns: 1fr;
  }
}
.app-sidebar {
  grid-area: s;
  min-width: 210px;
}
.app-content {
  grid-area: c;
  position: relative;
}

.Form {
  &_Row {
    margin: 0 0 1em;

    &__TextRight {
      text-align: right;
    }
  }
  &_Label {
    display: block;
    margin-bottom: .35em;
    font-size: .9em;
  }
  &_CheckboxLabel {
    font-size: .9em;
    margin-left: .35em;
  }

  &_Input {
    width: 100%;
    padding: .5em 1em;
    box-sizing: border-box;
    border: 0;
    border-radius: .25em;
    box-shadow: 0 0 0 1px $gray-color inset;
  }

  &_Textarea {
    width: 100%;
    padding: .5em 1em;
    box-sizing: border-box;
    border: 0;
    border-radius: .25em;
    box-shadow: 0 0 0 1px $gray-color;
  }

  &_Button {
    padding: .5em 1em;
    border: 0;
    border-radius: 3px;
    color: rgb(255, 255, 255);
    background: $main-color;
    cursor: pointer;
    outline: none;

    &:hover {
      background: darken($main-color, 10);
    }
    &[disabled] {
      cursor: default;
      background: $gray-color !important;
    }
  }
}
