@import "../../data";

.header {
  padding: 0 1em;
  grid-area: h;
  background-color: $main-color;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-radius: 2px;
}

.Login {
  display: flex;
  align-items: center;

  a {
    color: #fff;
  }
}
.Logout {
  margin-left: 1em;
  padding: 0;
  width: 1.5em;
  height: 1.5em;
  border: 0;
  cursor: pointer;
  background: none;
  transition: all .3s ease-in-out;
  outline: none;

  &:hover {
    transform: translateX(.25em);
  }
}
.LogoutIcon {
  fill: #fff;
  width: 1.5em;
  height: 1.5em;
}
